@import "constants.scss";

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  background-color: $white;
  color: #333;
  font-size: 15px;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
  'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
  sans-serif;
  font-weight: 400;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

button {
  background: none;
  border: none;
  outline: none;
  cursor: pointer;
}
button.btn {
  display: flex;
  align-items: center;
  justify-content: center;
}

img {
  outline-style: none;
  user-select: none;
  -webkit-user-drag: none;
  -webkit-tap-highlight-color: transparent;
}

* {
  -webkit-tap-highlight-color: transparent;
}

input[type=submit] {
  visibility: collapse;
}

input {
  background: none;
  border: none;
  outline: none;
}

#app {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  min-height: 100vh;
  margin: 0 auto;
}

.container {
  width: 100%;
  margin-right: auto;
  margin-left: auto;
}
@media (min-width: $screen-sm-min) {
  .container {
    max-width: 750px;
  }
}
@media (min-width: $screen-md-min) {
  .container {
    max-width: 970px;
  }
}
@media (min-width: $screen-lg-min) {
  .container {
    max-width: 1170px;
  }
}
@media (min-width: $screen-fhd-min) {
  .container {
    max-width: 1360px;
  }
}
.main-content {
  position: fixed;
  top: 56px;
  bottom: 0;
  left: 0;
  right: 0;
  background: url('../../public/images/chat_bg.png') no-repeat;
  background-size: cover;
  overflow: auto;
}

.icon {
  width: 16px;
  height: 16px;
}

.textarea {
  max-height: 100px;
  padding-right: 6px;
  overflow: auto;
  word-break: break-word;
  outline: none;
}

.textarea:focus + .textarea-placeholder{
  opacity: 0;
}

.textarea-placeholder {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  padding: 6px 12px;
  opacity: 0.5;
  transition: 0.2s ease;
  pointer-events: none;
}

.text-hidden {
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 1;
  overflow: hidden;
  text-overflow: ellipsis;
  word-break: break-word;
}

@keyframes appearance {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

select {
  background-color: transparent;
  padding: 0 1em 0 0;
  border: none;
  margin: 0;
  width: 100%;
  font-family: inherit;
  font-size: 16px;
  cursor: pointer;
  line-height: inherit;
  z-index: 1;
  outline: none;
}

.custom-select {
  display: grid;
  grid-template-areas: "select";
  align-items: center;
  position: relative;

  select,
  &::after {
    grid-area: select;
  }

  min-width: 15ch;
  max-width: 30ch;

  border: 2px solid $purple;
  border-radius: 12px;
  padding: 0.25em 0.5em;
  margin: 20px 0 30px;

  font-size: 1.25rem;
  cursor: pointer;
  line-height: 1.1;
}
